import Page from 'components/Page';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { Card, CardBody, CardHeader, Col, Row, Table, Form, Button } from 'reactstrap';

import * as actions from '../store/actions/index';
import DateRange from '../components/Filter/DateRange'
import AffiliateFilter from '../components/Filter/Affiliate'
import NotificationSystem from 'react-notification-system';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
import RowNoData from '../components/RowNoData';
import {
	MdError
} from 'react-icons/md';
import BasePage from './BasePage';
import formatDate from '../utils/formatDate';
import PaginationFooter  from '../components/Pagination/PaginationFooter';
import PaginationHeader from '../components/Pagination/PaginationHeader';
import NumberFormat from 'react-number-format';

const date = new Date();

class SelledProductPage extends BasePage {

	state = {
		filters: {
			affiliate_id: null,
			from: formatDate(new Date(date.getFullYear(), date.getMonth(), 1)),
			to: formatDate(new Date()),
		},
		paginateOptions:[10,20,30],
		currentPage:null,
		paginateElements:10
	}

	handleSubmit = () => {
		if (this.state.filters.affiliate_id === null) {
			this.notifyMissingField('Seleziona la filiale')
			return
		}
		if (this.state.filters.from === null) {
			this.notifyMissingField('Seleziona la data inizio')
			return
		}
		if (this.state.filters.to === null) {
			this.notifyMissingField('Seleziona la data fine')
			return
		}
		const { searchClicked } = this.state
		this.setState({
			searchClicked: !searchClicked
		})
		this.props.onLoadStats(this.state.currentPage,  this.state.paginateElements, this.state.filters, [], null)
	}

	handleFilterChange = (value, name) => {
		const { filters } = this.state
		filters[name] = value
		this.setState({
			filters
		})
	}

	handleChangePagination = (e) => {

		this.setState({ paginateElements: e }, () => {
				this.handleSubmit()
		});

	}

	handleChangePage = (e) => {

		this.setState({ currentPage: e }, () => {
				this.handleSubmit()
		});

	}

	notifyMissingField(message) {
		if (!this.notificationSystem) {
			return;
		}

		this.notificationSystem.addNotification({
			title: <MdError />,
			message: message,
			level: 'error',
		});
	}

	render() {
		let {
			stats
		} = this.props
		let pagination = null
		let rows = <RowNoData colspan={5} />
		if (this.props.loading === false && this.props.error === false && this.props.stats !== null) {
			rows = stats.map((row, index) => {
				return <tr key={index}>
					<td className="text-right">{row.attributes.barcode}</td>
					<td>{row.attributes.description}</td>
					<td className="text-right"><NumberFormat value={row.attributes.existence === null ? 0 : row.attributes.existence} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
					<td className="text-right"><NumberFormat value={row.attributes.quantity} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
				  <td className="text-right"><NumberFormat value={row.attributes.spent_amount} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
				</tr>
			})

		pagination = <PaginationFooter
		 currentPage={this.props.pagination != null ? this.props.pagination.current_page : null}
			lastPage={this.props.pagination != null ? this.props.pagination.last_page : null}
			handleChangePage={this.handleChangePage.bind(this)}/>
		}
		return (
			<Page title="Lista articoli" breadcrumbs={[{ name: 'lista articoli', active: true }]}>
				<Row>
					<Col>
						<Card className="mb-3">
							<CardHeader>
								Filtri
                            </CardHeader>
							<CardBody>
								<Form onSubmit={this.handleSubmit}>
									<DateRange onChange={this.handleFilterChange.bind(this)}  fromStartDate={this.state.filters.from} toStartDate={this.state.filters.to} />
									<Row>
										<AffiliateFilter onChange={this.handleFilterChange.bind(this)} />
									</Row>
									<Row>
										<Col>
											<Button
												color="primary"
												disabled={this.props.loading && this.state.searchClicked}
												onClick={this.handleSubmit}>
												Cerca
                                            </Button>
										</Col>
									</Row>
								</Form>
							</CardBody>
						</Card>
						<Card className="mb-3">
						<CardHeader>
								<Row>
										<Col  sm="12" md={{ size: 3, offset: 9}}>
											<PaginationHeader options={this.state.paginateOptions} handleChangePagination={this.handleChangePagination}/>
										</Col>
								</Row>
						</CardHeader>
							<CardBody>
								<Table responsive>
									<thead>
										<tr>
											<th>Barcode</th>
											<th>Descrizione</th>
											<th className="text-right">Esistenza</th>
											<th className="text-right">Venduto</th>
											<th className="text-right">Incassato</th>
										</tr>
									</thead>
									<tbody>
										{rows}
									</tbody>
								</Table>
							</CardBody>
						</Card>
					</Col>
				</Row>
				  {pagination}
				<NotificationSystem
					dismissible={false}
					ref={notificationSystem =>
						(this.notificationSystem = notificationSystem)
					}
					style={NOTIFICATION_SYSTEM_STYLE}
				/>
			</Page>
		);
	}
}
SelledProductPage.contextTypes = {
	store: PropTypes.object
};

SelledProductPage.propTypes = {
	notifications: PropTypes.array,
	stats: PropTypes.array,
	loading: PropTypes.bool,
	error: PropTypes.bool
};

const mapStateToProps = state => {
	return {
		stats: state.statistics.selledProductsStatistics,
		loading: state.statistics.loading,
		error: state.statistics.error,
		notifications: state.notifications,
		pagination: state.statistics.pagination,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onLoadStats: (page, perPage, filters, include, order) => dispatch(actions.loadSelledProductsStat(page, perPage, filters, include, order)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(SelledProductPage);
